.square {
  width: 100px;             
  height: 100px;             
  background-color: #f0f0f0; 
  border: 2px solid #999;    
  font-size: 24px;           
  cursor: pointer;         
  display: flex;             
  align-items: center;       
  justify-content: center;   
  transition: background-color 0.3s, transform 0.2s;
}
.highlight {
  background-color: yellow;
}
.board {
  display: grid;              
  grid-template-columns: repeat(3, 1fr); 
  width: 300px;               
  margin: auto;              
}
.square-value {
  font-weight: bold;              
  color: #333;                     
  text-shadow: 1px 1px 2px #fff;  
}